<template>
  <div class="zhilinPage">
      <h2 class="title">{{title}}</h2>
      <div class="box2">{{message}}</div>   


      <div class="ps">. 6v-if//v-show//v-else//v-for//v-bind//v-on (http://web.jobbole.com/87375/)</div>
      <div class="eg">
        <p :class="tit">1. v-if bool</p>
        <div id="example01">
            <p v-if="male">Male</p>
            <p v-if="female">Female</p>
            <p v-if="age>25">Age:{{age}}</p>
            <p v-if="name.indexOf('lin')>0">Name:{{name}}</p>
        </div>
        <p :class="result">v-iftrue</p>
      </div>

      <div class="eg">
        <p :class="tit">2. v-show  truthy  falsy  display CSS </p>
        <div id="example02">
            <p v-show="male">Male</p>
            <p v-show="female">Female</p>
            <p v-show="age>25">Age:{{age}}</p>
            <p v-show="name.indexOf('lin')>0">Name:{{name}}</p>
        </div>
        <p :class="result"> v-show ; v-iffalsecssstyle="display:none";</p>
      </div>

      <div class="eg">
        <p :class="tit">3. v-else v-if/v-show  v-if  v-else-if</p>
        <div id="example03">
            <p v-if="age3 >= 25">Age:{{age3}}</p>
            <p v-else>Name: {{name3}}</p>
            
            <p v-show="name3.indexOf('cool') == 0">Name: {{name3}}</p>
            <!-- <p v-else>Sex: {{sex3}}</p> --> 
            <!-- v-showv-else -->
        </div>
        <p :class="result"> v-if/v-showtrueelsev-if/v-showfalseelse</p>
        <p class="result">
            

            V-showV-if

            1V-ifV-showV-if

            2V-show
        </p>
      </div>

      <div class="eg">
        <p :class="tit">
            4. v-for JavaScript
            v-for="item in items"
            itemsitem
        </p>
        <div id="example04" :class="center">
            <table>
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Age</th>
                        <th>Sex</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="person in people" :key="person.id">
                        <td>{{person.name}}</td>
                        <td>{{person.age}}</td>
                        <td>{{person.sex}}</td>
                    </tr>
                </tbody>
            </table>
        </div>
        <p :class="result"> </p>
        
      </div>

      <div class="eg">
        <p :class="tit">
            5. v-bind  HTML classstyleeg,bind
        </p>
        <div id="example05" :class="center">
            <ul class="pagination">
                <li v-show="current != 1" @click="current-- && goto(current)" ><a href="javascript:void(0);"></a></li>
                <li v-for="index in pages" @click="goto(index)" :class="{'active':current == index}" :key="index">
                    <a href="javascript:void(0);" >{{index}}</a>
                </li>
                <li v-show="allpage != current && allpage != 0 " @click="current++ && goto(current++)"><a href="javascript:void(0);" ></a></li>
            </ul>
        </div>
        <p :class="result">https://www.cnblogs.com/cqsjs/p/5772834.html?utm_source=itdadao&utm_medium=referral </p>
        
      </div>

      <div class="eg">
        <p :class="tit">
            6. v-on DOM
        </p>
        <div id="example06" :class="center">
            <input type="text" v-model="imsg">
            <button v-on:click="greet">Greet</button>
            <!-- v-on@-->
            <!-- <button @click="greet">Greet Again</button> -->

            <div style="margin:0 20px;"></div>

            <input type="text" v-model="imsg2">
            <button v-on:click="showTitle(imsg2,$event,'hello')">Btn</button>
            <button @click="showTitle2">btn2</button>
            <span class="text">{{ititle2}}</span>

        </div>
        <p :class="result">V-on:click= vuemethods</p>
        <p :class="result">:HTML5v-on@</p>
        
      </div>


      <div class="ps">. </div>
      <div class="eg">
        <p :class="tit">
            7. v-text DOM
        </p>
        <div id="example07" :class="center">

        </div>
        <p :class="result">V-on:click= vuemethods</p>
        
      </div>













      <!--  -->
      <div id="Cqlsignature" style="margin:20px auto;">
        <p></p>
        <p class="Cqlsignature-name">by play</p>
      </div>
  </div>
</template>

<script>
    export default {
        name:'zhilin',
        data () {
            return {
                title:'',
                message:'sass',
                tit:'tit',
                result:'result',
                center:'center',

                male:true,
                female:false,
                age:29,
                name:'colin',

                age3:21,
                name3:'keepcool',
                sex3:'Male',
                people:[{
                    name:'Jack',
                    age:30,
                    sex:'Male'
                },{
                    name:'Bill',
                    age:26,
                    sex:'Male'
                },{
                    name:'Tracy',
                    age:22,
                    sex:'Female'
                },{
                    name:'Chris',
                    age:36,
                    sex:'Male'
                }],

                current:1,
                showItem:5,
                allpage:13,

                imsg:"Nice meeting U",

                imsg2:"Nice meeting U too",
                ititle2:''

            }

        },
        computed:{
            pages:function(){
                var pag = [];
                if( this.current < this.showItem ) {
                    var i = Math.min(this.showItem,this.allpage);
                    while(i){
                        pag.unshift(i--);
                    }
                }else {
                    var middle = this.current -Math.floor(this.showItem / 2),
                        i = this.showItem;
                    if( middle > (this.allpage - this.showItem) ){
                        middle = (this.allpage - this.showItem) + 1
                    }
                    while(i--){
                        pag.push( middle++ );
                    }
                }
                return pag
            }
        },
        methods:{
            goto:function(index){
                if(index == this.current) return;
                this.current = index;
                //ajax
            },
            greet:function(){
                alert(this.imsg);
            },
            showTitle:function(imsg2, event, msg) {
                event.preventDefault()
                
                this.ititle2 = '';
                this.msg = " hello";
                this.ititle2 = this.imsg2 + this.msg;
                alert('hello' + this.ititle2 + '!')
            },
            showTitle2:function() {
                // console.log(this)
                // console.log(arguments)        msg, $event, 'hello'
                // thisvueData.title   data.aa
                
                this.ititle2 = '';
                this.ititle2 = this.imsg2;
            },
        }
    }
</script>

<style lang="scss">
$f12: 12px;

$bgCol:red;

$col_white:#ffffff;
$blue:#177bbb;
$red:red;

@mixin border-radius{
    -webkit-border-radius: 5px;
    border-radius: 5px;
}

@mixin borderBottom($w,$s,$c){
  border-bottom:$w $s $c;
}

// https://www.jianshu.com/p/680aea02eba4 (sass)
@mixin prefixer($name,$shadow) {
  @if($name=='border-radius'){
    border-radius: $shadow;
  } @else if($name=='box-shadow'){
    box-shadow:$shadow;
  }
}
@mixin box-shadow($shadow...) {
  @if length($shadow) >= 1 {
    @include prefixer(box-shadow, $shadow);
  } @else{
    $shadow:0 0 4px rgba(0,0,0,.3);
    @include prefixer(box-shadow, $shadow);
  }
}

@mixin size($width,$height){
  width: $width;
  height: $height;
}

@mixin lh($lh){
  line-height:$lh;
}

@mixin fontSize($f){
  font-size:$f;
}
/* */
@keyframes ants { to { background-position: 100% 100% } }

#Cqlsignature {
	padding: 1em;
	border: 1px solid transparent;
	background: linear-gradient(white, white) padding-box,
	            repeating-linear-gradient(-45deg, black 0, black 25%, transparent 0, transparent 50%) 0 / .6em .6em;
	animation: ants 15s linear infinite;
	max-width:20rem;
	
	font: 100%/1.6 Baskerville, Palatino, serif;
}
.Cqlsignature-name{
   text-align:right;
   padding-right:2rem
}

.zhilinPage {
    font-size:$f12;
    padding-top:30px;
}

.box2 {
    @include border-radius;
    @include size(200px,50px);
    @include box-shadow(0 0 1px rgba(#000,.5));
    background:$bgCol;
    color:$col_white;
    @include lh(50px);
    margin:0 auto;
}
.zhilinPage .ps {
    padding-top:20px;
    @include fontSize(14px);
}
.zhilinPage .eg {
    @include borderBottom(1px, solid, #ccc);
}
.zhilinPage .tit {
    @include fontSize(16px);
    color:$blue;
}
.zhilinPage .result {
    color:$blue;
}

.zhilinPage .center {
    display: flex;
    justify-content:center;
    width:100%;
}

// 
body{
font-family:"Segoe UI";
}
li{
list-style:none;
}
a{
text-decoration:none;
}
.pagination {
position: relative;

}
.pagination li{
display: inline-block;
margin:0 5px;
}
.pagination li a{
padding:.5rem 1rem;
display:inline-block;
border:1px solid #ddd;
background:#fff;

color:#0E90D2;
}
.pagination li a:hover{
background:#eee;
}
.pagination li.active a{
background:#0E90D2;
color:#fff;
}

.zhilinPage .text {padding:5px 10px;color:$red;}

</style>




