<template>
 <div class="sub-container">
    <h3></h3>

    <div></div>
    <div>{{sendMessage}}</div>
    <div>{{sendNum}}</div>

    <div></div>
    <input type="text" v-model="sendMessage">
    <!-- <input type="text" v-model="sendNum"> -->

     <ul v-for="item of sonList" :key="item.id">
      <li>{{item.name}}</li>
    </ul>
 </div>
</template>

<script>
//
export default {
 name:'child',//nameimport
 props:{
   sendMessage:{
     type:String,
     required:true
   },
   sendNum:{
     type:Number,
     required:true
   },
   sendObj:{
     validator:function(val){
       if(Object.prototype.toString.call(val) === '[object Array]' || Object.prototype.toString.call(val) === '[object Object]'){
         return true;
       }
     }
   }
 },
 data(){
  //  
  /* var parentObj = this.sendObj;//json
  var childObj = JSON.parse(JSON.stringify(parentObj));//
  childObj.push({id:3,name:'mike'});//
  return {
    sonList:childObj
  }; */

// watch 
  return {
    sonList:null
  };
 },
 watch:{//sendObj
  sendObj(newval,oldval){

   if(newval !== oldval){

    var newobj =JSON.parse(JSON.stringify(newval));//
    /* 
    parsejson
    stringify()
    */
    newobj.push({id:3,name:'mike'});//
    this.sonList = newobj;//datalist(sonList)
   }

  }
 }
}
</script>

<style>
.sub-container{
 background: rgb(229, 229, 252);
 color:#177bbb;
}
</style>



